@font-face {
  font-family: "Cyberthrone";
  src: url("../app/fonts/cyberthrone.woff2");
}
:root {
  --font-body: "Cyberthrone";
  --transition-min: .2s;
  --transition-max: 8s;
}

:root {
  --white: hsl(0, 0%, 100%);
  --aqua: hsl(179, 100%, 50%);
  --cherry: hsl(327, 88%, 37%);
  --light-gray: hsl(0, 0%, 20%);
  --dark-gray: hsl(0, 0%, 8%);
  --black: hsl(0, 0%, 0%);
}

:root {
  --animate-delay: 1s;
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

.delay-1 {
  animation-delay: var(--animate-delay);
}

.delay-2 {
  animation-delay: calc(var(--animate-delay) * 1.2);
}

.delay-3 {
  animation-delay: calc(var(--animate-delay) * 1.3);
}

.delay-4 {
  animation-delay: calc(var(--animate-delay) * 1.4);
}

.delay-5 {
  animation-delay: calc(var(--animate-delay) * 1.5);
}

.delay-6 {
  animation-delay: calc(var(--animate-delay) * 1.6);
}

.delay-7 {
  animation-delay: calc(var(--animate-delay) * 1.7);
}

.delay-8 {
  animation-delay: calc(var(--animate-delay) * 1.8);
}

.delay-9 {
  animation-delay: calc(var(--animate-delay) * 1.9);
}

.delay-10 {
  animation-delay: calc(var(--animate-delay) * 2);
}

.delay-11 {
  animation-delay: calc(var(--animate-delay) * 2.1);
}

.delay-12 {
  animation-delay: calc(var(--animate-delay) * 2.2);
}

.delay-13 {
  animation-delay: calc(var(--animate-delay) * 2.3);
}

.delay-14 {
  animation-delay: calc(var(--animate-delay) * 2.4);
}

.delay-15 {
  animation-delay: calc(var(--animate-delay) * 2.5);
}

.fadeIn {
  animation-name: fadeIn;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-size: 1.875rem;
  font-family: var(--font-body);
  color: var(--white);
  line-height: 1.3;
  overflow: hidden;
  cursor: url("../images/tools/normal.cur"), auto;
}

svg {
  cursor: url("../images/tools/busy.cur"), auto;
}

.wb-body {
  background-color: var(--dark-gray);
  color: var(--white);
  padding: 20px;
}

.section__title, .section__subtitle {
  cursor: url("../images/tools/busy.cur"), auto;
}
.section__links {
  display: inline-flex;
}
.section__links:first-child a {
  color: var(--cherry);
}
.section__links:first-child a:not(:last-child) {
  padding-right: 1.25rem;
}
.section__links:last-child {
  flex-direction: column;
}

#click {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 2;
  background-color: var(--black);
}
#click::after {
  content: "";
  width: 37.5rem;
  height: 20.9375rem;
  background: var(--black) url("../images/loading.webp") no-repeat 50%/contain;
}

h1,
h2,
h3,
p {
  margin: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--aqua);
}

a:hover,
#click {
  cursor: url("../images/tools/link.cur"), auto;
}

.wrapper {
  height: 100vw;
}
@media (min-width: 40em) {
  .wrapper {
    transform: rotate(-90deg) translateX(-100vh);
    transform-origin: top left;
    overflow-x: scroll;
  }
  .wrapper::-webkit-scrollbar {
    display: none;
  }
  .wrapper__inner {
    display: flex;
    transform: rotate(90deg) translateY(-100vh);
    transform-origin: top left;
  }
}

.container {
  display: flex;
  flex-direction: column;
  position: absolute;
}
@media (min-width: 40em) {
  .container {
    flex-direction: row;
    align-items: center;
  }
}
.container__hero {
  position: relative;
  bottom: -1.75rem;
}
@media (min-width: 40em) {
  .container__portfolio {
    max-width: 71.25rem;
  }
}

.section__scroll {
  overflow: scroll;
}
.section__scroll::-webkit-scrollbar {
  display: none;
}

.slide {
  display: flex;
  flex-direction: column;
  background-image: url("../images/background.webp");
  height: 100vh;
}
.slide.one {
  align-items: center;
  justify-content: center;
}
.slide.two {
  align-items: flex-end;
  background-image: none;
  background-color: var(--light-gray);
  box-shadow: -8.75rem -2.5rem 2.5rem var(--dark-gray);
  height: 200vh;
}
.slide::before {
  content: "";
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
}

.hidden {
  display: none;
}

.section {
  position: relative;
  padding-top: 4.375rem;
  padding-left: 1.875rem;
}
@media (min-width: 40em) {
  .section {
    padding: 0;
    top: 0;
  }
  .section .section__title {
    font-size: 9.375rem;
  }
  .section .section__subtitle {
    font-size: 2.875rem;
    margin-bottom: 3.4375rem;
  }
}
.section__title {
  position: relative;
  font-size: 7.8125rem;
  margin: 0.3125rem 0;
  transform: translate(0%, 0%);
  line-height: 0.8;
}
.section__title:before, .section__title:after {
  content: "codlipa";
  position: absolute;
  left: 0;
  opacity: 0.8;
}
.section__title:after {
  color: var(--cherry);
  z-index: -2;
}
.section__title:before {
  color: var(--aqua);
  z-index: -1;
}
.section__title:hover:before {
  animation: glitch var(--transition-min) cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
}
.section__title:hover:after {
  animation: glitch var(--transition-min) cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite reverse;
}
.section__subtitle {
  font-size: 2.25rem;
  font-weight: 700;
  margin-bottom: 0.9375rem;
  background-image: linear-gradient(var(--cherry), var(--aqua));
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

@keyframes glitch {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translate(-0.3125rem, 0.3125rem);
  }
  40% {
    transform: translate(-0.3125rem, -0.3125rem);
  }
  60% {
    transform: translate(0.3125rem, 0.3125rem);
  }
  80% {
    transform: translate(0.3125rem, -0.3125rem);
  }
  100% {
    transform: translate(0);
  }
}
.hero {
  display: flex;
  align-items: flex-end;
  overflow: hidden;
  height: 100vh;
}
.hero::before {
  content: "";
  position: relative;
  left: 3.75rem;
  bottom: -2.5rem;
  min-height: 24.5rem;
  min-width: 46.125rem;
  transform: rotate(90deg);
  background: url("../images/console.webp") 50%/contain;
}
@media (min-width: 40em) {
  .hero::before {
    left: 0;
    transform: rotate(0);
  }
}
.hero::after {
  content: "";
  position: absolute;
  left: 45%;
  bottom: 15rem;
  transform: scaleX(-1);
  min-width: 31.25rem;
  min-height: 26.5625rem;
  background-image: url("../images/avatar.webp");
}

.portfolio {
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
}
.portfolio__link {
  width: 23.75rem;
  height: 15.625rem;
  overflow: hidden;
  flex-grow: 1;
}
.portfolio__image {
  height: 100%;
  background-position: 50%;
  background-size: cover;
  transition: var(--transition-min);
}
.portfolio__image:hover {
  transform: scale(1.2);
}
.portfolio__image.soon {
  cursor: url("../images/tools/unavailable.cur"), auto;
  background-image: url("https://via.placeholder.com/380x250?text=Coming+Soon");
}
.portfolio__image.item-1 {
  background-image: url("../images/content/item-1.webp");
}
.portfolio__image.item-2 {
  background-image: url("../images/content/item-2.webp");
}
.portfolio__image.item-3 {
  background-image: url("../images/content/item-3.webp");
}
.portfolio__image.item-4 {
  background-image: url("../images/content/item-4.webp");
}
.portfolio__image.item-5 {
  background-image: url("../images/content/item-5.webp");
}
.portfolio__image.item-6 {
  background-image: url("../images/content/item-6.webp");
}